.footer-mobile-body {
    background-color: #2c3741;
    color: #f1f1f1;
    display: flex;
    flex-direction: column;
    text-align: justify;
    width: 100%;
}

.footer-mobile-container-left {
    margin: auto;
    width: 80%;
    margin-top: 0;
}

.footer-mobile-container-left h1 {
    font-size: 12vw;
    letter-spacing: 3vw;
    padding: 0;
    margin: 1rem auto 1vw 0;
    line-height: 6rem;
    text-align: center;
}

.footer-mobile-container-left p {
    font-size: 0.7rem;
    max-width: 30rem;
    margin: auto;
}

.footer-mobile-container-right {
    margin: 1rem;
    text-align: center;
}

.footer-mobile-container-right-boka, .footer-mobile-container-right-boka a {
    font-size: 15vw;
    letter-spacing: 3vw;
    background-color: #f1f1f1;
    color: #2c3741;
    width: fit-content;
    margin: 2rem auto;
    padding: 0 3vw;
    border-radius: 7vw;
    text-decoration: none;
}

.footer-mobile-social-media-icons-wrapper {
    display: flex;
    justify-content: center;
}

.footer-mobile-social-media-icons-wrapper a {
    padding: 1rem 1rem 0 1rem;
    font-size: 18vw;
    color: rgb(218, 211, 127);
}

.footer-mobile-copyright {
    font-size: 0.7rem;
    text-align: center;
    margin-bottom: 1rem;
}